import OtherLogin from './OtherLogin';
export default {
  props: ["data", "platformArr"],
  components: {
    OtherLogin: OtherLogin
  },
  methods: {
    signIn: function signIn() {
      //登录
      this.$emit('signIn');
    },
    openSingUp: function openSingUp() {
      this.$emit('openSingUp');
    },
    forgotPS: function forgotPS() {
      this.$emit('forgotPS');
    },
    selectLogin: function selectLogin(platform) {
      this.$emit('selectLogin', platform);
    }
  }
};