export default {
  props: ["platformArr"],
  methods: {
    selectLogin: function selectLogin(k) {
      var object = '';

      switch (k) {
        case 'lazada':
          object = '1008';
          break;

        case 'shopify':
          object = '1007';
          break;

        default:
          break;
      }

      this.$Burying({
        object: object
      });

      if (this.platformArr[k].enabled) {
        if (k == 'shopify') {
          this.$router.push({
            name: 'login',
            query: {
              platform: 'shopify'
            }
          });
        } else {
          this.$emit('selectLogin', k);
        }
      }
    }
  }
};