export default {
  props: ["loading", "platformArr", "activePlatform", "shopUrl"],
  data: function data() {
    return {
      shopUrlTxt: ''
    };
  },
  mounted: function mounted() {
    this.shopUrlTxt = this.shopUrl;
  },
  watch: {
    shopUrl: function shopUrl() {
      this.shopUrlTxt = this.shopUrl;
    }
  },
  methods: {
    selectPlatform: function selectPlatform(p) {
      if (this.loading) return;
      this.$emit('selectPlatform', p);
    },
    gotoLogin: function gotoLogin() {
      if (this.platformArr[this.activePlatform].enabled) this.$emit('gotoLogin', this.shopUrlTxt);
    }
  }
};