export default {
  props: ["data", "sec"],
  data: function data() {
    return {
      emailLoading: false
    };
  },
  watch: {
    'data.isShow': function dataIsShow() {
      var _this = this;

      if (this.data.isShow) {
        setTimeout(function () {
          _this.data.isReadonly = false;
        }, 500);
      }
    }
  },
  methods: {
    saveNewPSD: function saveNewPSD() {
      this.$emit('saveNewPSD');
    },
    sendToken: function sendToken() {
      var _this2 = this;

      this.emailLoading = true;
      this.$apiCall('api.User.sendToken', {
        mobile: this.data.email,
        reset: true,
        lang: "EN"
      }, function (r) {
        _this2.emailLoading = false;

        if (r.ErrorCode == 9999) {
          _this2.data.isReadonly = false;

          _this2.$emit('resetSEC', 'forgot');
        } else {
          _this2.$refs.verify.reset();

          _this2.$message({
            message: r.Message,
            type: "error"
          });
        }
      });
    },
    verifySuccess: function verifySuccess() {
      if (this.data.email && this.checkEmail()) {
        this.sendToken();
      } else {
        this.$refs.verify.reset();
        this.$message({
          message: 'Please enter a valid Email.',
          type: "error"
        });
      }
    },
    checkEmail: function checkEmail() {
      var reg = new RegExp("^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$"); //正则表达式

      if (this.data.email === "") {
        //输入不能为空
        return false;
      } else if (!reg.test(this.data.email)) {
        //正则验证不通过，格式不对
        return false;
      } else {
        return true;
      }
    }
  }
};